<template>
  <div class="pay">
    <div class="paytype">
      <p class="paytype__title">支付方式</p>
      <div class="paytype__item">
        <div
          class="paytype__item__list"
          v-for="(item,index) in payTypeData"
          :key="index">
          <div>
            <img :src="item.iconType">
            <p>{{ item.name }}</p>
          </div>
          <img :src="currentType===index?item.iconSel:item.iconNoSel" @click="switchSel(index,item.type)">
        </div>
      </div>
    </div>
    <footer>
      <!-- 确认支付 -->
      <div class="pay">
        <div class="pay__left">
          <span class="pay__cell">¥</span>
          <span class="pay___num">12</span>
        </div>
        <div class="pay__btn" @click="pay">确认支付</div>
      </div>
    </footer>
  </div>
</template>
<script>
import { getSearch } from '@/utils'
export default {
  data () {
    return {
      payTypeData: [
        {
          iconType: require('@/assets/giftBoxPay/icon_wx.png'),
          name: '微信支付',
          iconSel: require('@/assets/giftBoxPay/icon_sel.png'),
          iconNoSel: require('@/assets/giftBoxPay/icon_nosel.png'),
          type: 2
        },
        {
          iconType: require('@/assets/giftBoxPay/icon_zfb.png'),
          name: '支付宝支付',
          iconSel: require('@/assets/giftBoxPay/icon_sel.png'),
          iconNoSel: require('@/assets/giftBoxPay/icon_nosel.png'),
          type: 1
        }
      ],
      pay_type: 2, // 支付方式  1支付宝  2微信  默认是微信
      currentType: 0
    }
  },
  methods: {
    switchSel (index, type) {
      this.currentType = index
      this.pay_type = type
    },
    // 支付
    pay (payType) {
      const query = getSearch()
      const url = localStorage.getItem('herf')
      const phone = localStorage.getItem('LiveEnhance12-phone')
      const code = localStorage.getItem('LiveEnhance12-code')
      this.$apis.payment
        .pay({
          ...query,
          phone,
          code,
          pay_type: this.pay_type,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_12_WM_LIVE'
        })
        .then((data) => {
          if (this.pay_type === 1) {
            console.log(this.pay_type, data)
            this.aliPay(data)
          }
          if (this.pay_type === 2) {
            this.wxPay(data)
          }
        })
        .catch((err) => {
          if (err.msg) {
            this.$dialog({ message: err.msg })
          }
        })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    }
  }
}
</script>
<style lang="scss" scoped>
// footer
footer {
  background-color: #fff;
  // 支付信息
  .pay {
    width: 750px;
    height: 120px;
    background: #fff;
    padding: 14px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .pay__left {
      display: flex;

      .pay__cell {
        font-size: 32px;
        color: #fe2f2a;
        line-height: 28px;
        font-weight: 700;
        padding-top: 16px;
      }

      .pay___num {
        font-weight: 700;
        font-size: 64px;
        color: #fb4237;
        margin-right: 18px;
        line-height: 40px;
      }
    }

    .pay__btn {
      width: 372px;
      height: 86px;
      background: linear-gradient(90deg, #fe612d 0%, #fe2d2a 100%);
      box-shadow: 0 4px 8px 0 rgba(254, 50, 42, 0.16);
      border-radius: 48px;
      color: #fff;
      font-size: 40px;

      @include horVerCenter();
    }
  }
}
// 支付方式
.paytype {
  width: 690px;
  background: #fff;
  margin: 0 auto;
  border-radius: 14px;
  margin-top: 30px;
  // margin-bottom: 226px;
  padding: 32px 44px 50px 36px;
  .paytype__title {
    font-size: 28px;
    color: #999;
  }
  .paytype__item {
    margin-top: 26px;
  }

  .paytype__item__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 28px;
    color: #333;

    &:last-child {
      margin-top: 16px;
    }

    & > img {
      width: 38px;
      height: 38px;
    }

    & > div {
      @include verCenter();

      display: flex;

      & > img {
        width: 64px;
        height: 64px;
        margin-right: 54px;
      }
    }
  }
}
</style>
